import {Avatar} from "@material-ui/core";
import React from 'react';
import "./StoryAdd.css";
import firebase from "firebase";
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';
import Button from '@material-ui/core/Button';
import db, {storage} from "./firebase";
import {useStateValue} from "./StateProvider";

function StoryAdd() {
        const [{user}, dispatch] = useStateValue();
		const [loading, setLoading] = React.useState(false);
		const [progress1,setProgress1]= React.useState(0);
		
	const handleStoryOpen = () => {
    setOpen(true);
	    if(navigator.userAgent.match(/IEMobile|Windows/i) != "Windows")
		{
			setMaxWidth1(true);
		}
  };
	const handleStoryClose = () => {
    setOpen(false);
  }; 
  
  const handleDial0 = () => {
	  if(navigator.userAgent.match(/IEMobile|Windows/i) != "Windows")
	  {
	   
	  }
  }
  
  const handlePrev1a = () => {
			document.getElementById('previewImg123').src = URL.createObjectURL(document.getElementById('contained-button-file123').files[0]);		
	}
  
  const [open, setOpen] = React.useState(false);
  const [fullWidth, setFullWidth] = React.useState(true);
  const [maxWidth1, setMaxWidth1] = React.useState(false);  
  
	const handleFilea = () => {
		if(( document.getElementById('previewImg123').src != "" )) {
	var file = document.getElementById('contained-button-file123').files[0];
	var filenam = file.name;
	var metadata = {
  contentType: 'image/jpeg',
};
var uploadTask = storage.child('images/' + file.name).put(file, metadata);

uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
  function(snapshot) {
    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
    var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
	setProgress1(progress);
    console.log('Upload is ' + progress + '% done');
	if(progress == 100)
	{
		setLoading(false);
	}
    switch (snapshot.state) {
      case firebase.storage.TaskState.PAUSED: // or 'paused'
        console.log('Upload is paused');
        break;
      case firebase.storage.TaskState.RUNNING: // or 'running'
        console.log('Upload is running');
        break;
    }
  }, function(error) {

  switch (error.code) {
    case 'storage/unauthorized':
      break;

    case 'storage/canceled':
      break;

    case 'storage/unknown':
      break;
  }
}, function() {
  uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
    console.log('File available at', downloadURL);
	alert("Your story added successfully");
            db.collection('stories').add ({
                storypic: user.photoURL,
                storyname: user.displayName,
                storyphoto: downloadURL,
                storytime: firebase.firestore.FieldValue.serverTimestamp() ,
            })    
  });
});
	setOpen(false);
	}
	else{
			setOpen(false);
	}
		}
	
	
	
    return (
	<React.Fragment>
        <div style={{ backgroundImage: `url(${""})`}} className="story1" onClick={handleStoryOpen}>
            <Avatar className="story_avatar1" src={user.photoURL} />
            <h6>Add your story here</h6>
        </div>
		<Dialog
					fullWidth={fullWidth}
        			maxWidth={'xs'}
        			open={open}
        			onClose={handleStoryClose}
        			aria-labelledby="max-width-dialog-title"
      			>
		<DialogTitle id="max-width-dialog-titlea">Add Your Story</DialogTitle>
				<DialogContent>
      <div className="dialog0a" onLoad={handleDial0}>
				<img  id="previewImg123" className="dialog0a-img"  src="" />
				<div className="dialog1a" >
				<input
        				accept="image/*"
        				className="upFile"
        				id="contained-button-file"
        				multiple
        				type="file"
						style={{display:'none'}}
						onChange={handlePrev1a}
      			/>
				<label htmlFor="contained-button-file123">
					<Button variant="outlined" color="primary" size={maxWidth1 ? 'small':'medium'} component="span" >
						Choose image
					</Button>
				</label>
					<Button variant="outlined" color="primary" size={maxWidth1 ? 'small':'medium'} onClick={handleFilea} >
						Add to your Story
					</Button>
				</div>
		</div>
		</DialogContent>
		</Dialog>
	</React.Fragment>
    )
}

export default StoryAdd
