import React, { useEffect, useState } from 'react' ;
import "./StoryReel.css";
import Story from "./Story";
import StoryAdd from "./StoryAdd";
import { useStateValue } from "./StateProvider";
import db  from "./firebase";

function StoryReel() {
    const [{user },dispatch] = useStateValue();
	const [stories,setStories] = useState([]);
    useEffect(() =>{
      db.collection("stories").orderBy("storytime","desc").onSnapshot(snapshot => (
          setStories(snapshot.docs.map((doc) => ({ id: doc.id, data: doc.data()})))
      ));
  }, [])
    return (
        <div className="storyReel">
			<StoryAdd />
			{ stories.map(story => (
            <Story image={story.data.storyphoto}
            profileSrc={story.data.storypic}
             title={story.data.storyname} />
            ))}
        </div>
    );
}

export default StoryReel
