import React, {useEffect, useState} from 'react';
import "./Feed.css";
import StoryReel from "./StoryReel";
import MessageSender from "./MessageSender";
import Post from './Post';
import db from "./firebase";


function Feed() {
    const [posts,setPosts] = useState([]);

    useEffect(() =>{
        db.collection("posts").orderBy("timestamp","desc").onSnapshot(snapshot => (
            setPosts(snapshot.docs.map((doc) => ({ id: doc.id, data: doc.data()})))
        ));
    }, [])
    return (
        <div className="feed">
            <StoryReel />
            <MessageSender />
            { posts.map(post => (
                <Post
                    key={post.id}
                    profilePic={post.data.profilePic}
                    message={post.data.message}
                    timestamp={post.data.comstdtime}
                    username={post.data.username}
                    image={post.data.image}
                    video={post.data.video}
                    like={post.data.like}
                    comment={post.data.comment}
                    share={post.data.share}
                    totlik={post.data.totlik}
                    totcom={post.data.totcom}
                    totsha={post.data.totsha}
                    posid={post.id}
                    comarr={post.data.comarr}
                    comtime={post.data.comtime}
                    comuser={post.data.comuser}
                    compic={post.data.compic}
                />
                )) 
            }
        </div>
    );
}

export default Feed
