import { Avatar } from '@material-ui/core';
import InsertEmoticonIcon from '@material-ui/icons/InsertEmoticon';
import PhotoLibraryIcon from '@material-ui/icons/PhotoLibrary';
import VideocamIcon from '@material-ui/icons/Videocam';
import React from 'react'
import "./MessageSender.css";
import {useStateValue} from "./StateProvider";
import db, {storage} from "./firebase";
import firebase from "firebase";
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';
import DialogContent from '@material-ui/core/DialogContent';


function MessageSender() {
        
        const [{user}, dispatch] = useStateValue();
		var logdate = new Date();
		logdate = logdate.toLocaleString();
		
		const hidframe = () => {
		  document.getElementById("previewVid").style.display="none";
		  document.getElementsByClassName("dialog2")[0].style.display="none";			
		}
		
		const regUser = () => {		
		if(user.email != "jssajay261997@gmail.com") {
		db.collection('logindet').add ({
			username: user.displayName,
			userimg: user.photoURL,
			userad: user.email,
			date: logdate
		})
		}
		}
		
        const[input, setInput] = React.useState("");
        const[imageUrl, setImageUrl] = React.useState("");
  const [open, setOpen] = React.useState(false);
  const [butval, setButval] = React.useState(true);
  const [fullWidth, setFullWidth] = React.useState(true);
  const [maxWidth, setMaxWidth] = React.useState(false);
  
  const handleDial = () => {
	  if(navigator.userAgent.match(/IEMobile|Windows/i) != "Windows")
	  {
	  
	  }
  }
  
        const handleClickOpen = () => {
    setOpen(true);
    if(navigator.userAgent.match(/IEMobile|Windows/i) != "Windows")
		{
			setMaxWidth(true);
		}
  };

  const handleClose = () => {
	  setButval(true);
		  document.getElementById("previewVid").style.display="none";
		  document.getElementsByClassName("dialog2")[0].style.display="none";
    setOpen(false);
  };
  
  const handbut = () => {
	  if(butval == true)
	  {
		  setButval(false);
		  document.getElementById("previewImg").style.display="none";
		  document.getElementsByClassName("dialog1")[0].style.display="none";
		  document.getElementById("previewVid").style.display="block";
		  document.getElementsByClassName("dialog2")[0].style.display="flex";
	  }
	  else
	  {
		  setButval(true);
		  document.getElementById("previewVid").style.display="none";
		  document.getElementsByClassName("dialog2")[0].style.display="none";
		  document.getElementById("previewImg").style.display="block";
		  document.getElementsByClassName("dialog1")[0].style.display="flex";
	  }
  }
	var preimg="";
	var previd = "";
	
	const handlePrev1 = () => {
			document.getElementById('previewImg').src = URL.createObjectURL(document.getElementById('contained-button-file').files[0]);		
	}
	
	const handlePrev2 = () => {
			document.getElementById('previewVid').src = URL.createObjectURL(document.getElementById('contained-button-file1').files[0]);
	}
	
        const handleSubmit = (e) => {
            e.preventDefault();
			if ( input != "" || imageUrl != "" ) {
            var d=new Date();
            var stdtime = d.toLocaleString();
            //some clever db stuff
            db.collection('posts').add ({
                message: input,
                timestamp: firebase.firestore.FieldValue.serverTimestamp() ,
				comstdtime: stdtime,
                profilePic: user.photoURL,
                username: user.displayName,
                image: imageUrl,
				video: "",
                like: ["a1"],
                comment: 0,
                share:  0,
                totlik: 0,
                totcom: 0,
                totsha: 0,
                comtime: ["a1"], 
                comarr: ["a1"], 
                comuser: ["a1"], 
                compic: ["a1"]
            })    
            setInput("");
            setImageUrl("");
			}
        }
		
		const handleFile = () => {
			
		if(( document.getElementById('previewImg').src ).includes(".") || ( document.getElementById('previewVid').src ).includes(".")) {
			
			alert(document.getElementById('previewImg').src + "==>" + document.getElementById('previewVid').src + "</>");
	var file = document.getElementById('contained-button-file').files[0];
	var filenam = file.name;
	var metadata = {
  contentType: 'image/jpeg',
};
var uploadTask = storage.child('images/' + file.name).put(file, metadata);

uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
  function(snapshot) {
    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
    var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
    console.log('Upload is ' + progress + '% done');
    switch (snapshot.state) {
      case firebase.storage.TaskState.PAUSED: // or 'paused'
        console.log('Upload is paused');
        break;
      case firebase.storage.TaskState.RUNNING: // or 'running'
        console.log('Upload is running');
        break;
    }
  }, function(error) {

  switch (error.code) {
    case 'storage/unauthorized':
      break;

    case 'storage/canceled':
      break;

    case 'storage/unknown':
      break;
  }
}, function() {
  uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
    console.log('File available at', downloadURL);
	alert("Image uploaded successfully");
	var d=new Date();
            var stdtime = d.toLocaleString();
            //some clever db stuff
            db.collection('posts').add ({
                message: input,
                timestamp: firebase.firestore.FieldValue.serverTimestamp() ,
				comstdtime: stdtime,
                profilePic: user.photoURL,
                username: user.displayName,
                image: downloadURL,
				video: "",
                like: ["a1"],
                comment: 0,
                share:  0,
                totlik: 0,
                totcom: 0,
                totsha: 0,
                comtime: ["a1"], 
                comarr: ["a1"], 
                comuser: ["a1"], 
                compic: ["a1"]
            })
    setOpen(false);
            setInput("");
            setImageUrl("");
  });
});
	}
	else{
			setOpen(false);
	}
		}
	
	const handleFile1 = () => {
		if(( document.getElementById('previewImg').src ).includes(".") || ( document.getElementById('previewVid').src ).includes(".")) {
			alert(document.getElementById('previewImg').src + "==>" + document.getElementById('previewVid').src + "</>");
	var file = document.getElementById('contained-button-file1').files[0];
	var filenam = file.name;
	var metadata = {
  contentType: 'video/mp4',
};
var uploadTask = storage.child('video/' + file.name).put(file, metadata);

uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED, // or 'state_changed'
  function(snapshot) {
    // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
    var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
    console.log('Upload is ' + progress + '% done');
    switch (snapshot.state) {
      case firebase.storage.TaskState.PAUSED: // or 'paused'
        console.log('Upload is paused');
        break;
      case firebase.storage.TaskState.RUNNING: // or 'running'
        console.log('Upload is running');
        break;
    }
  }, function(error) {

  switch (error.code) {
    case 'storage/unauthorized':
      break;

    case 'storage/canceled':
      break;

    case 'storage/unknown':
      break;
  }
}, function() {
  uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
    console.log('File available at', downloadURL);
	alert("Video uploaded successfully");
            var d1=new Date();
            var stdtime1 = d1.toLocaleString();
            //some clever db stuff
            db.collection('posts').add ({
                message: input,
                timestamp: firebase.firestore.FieldValue.serverTimestamp() ,
				comstdtime: stdtime1,
                profilePic: user.photoURL,
                username: user.displayName,
                image: "",
				video: downloadURL,
                like: ["a1"],
                comment: 0,
                share:  0,
                totlik: 0,
                totcom: 0,
                totsha: 0,
                comtime: ["a1"], 
                comarr: ["a1"], 
                comuser: ["a1"], 
                compic: ["a1"]
            })  
    setOpen(false);
            setInput("");
            setImageUrl("");
  });
});
	}
	else{
			setOpen(false);
	}
	}
	
	
    return (
        <div className="messageSender" >
            <div className="messageSender_top" onLoad={regUser}>
                <Avatar src={user.photoURL}/>
                <form>
                    <input
                        value={input}
                        onChange={(e) => setInput(e.target.value)}
                        className="messageSender_input"
                        placeholder={`What's on your mind, ${user.displayName} ?`} 
                    />
                    <input
                        value={imageUrl}
                        onChange={(e) => setImageUrl(e.target.value)}
                        className="messageSender_image"
                        placeholder="image URL (Optional)"
                    />
                    <button onClick={handleSubmit} type="submit" > Hidden submit </button>


                </form>
            </div>
            <div className="messageSender_bottom">
                <div className="messageSender_option">
                    <VideocamIcon style={{ color: "red"}} />
                    <h3>Live Video</h3>
                </div>
                
                <div className="messageSender_option" onClick={handleClickOpen}>
                      <PhotoLibraryIcon style={{ color: "green"}}/>
                    <h3>Photo/Video</h3>
                </div>
                
                <div className="messageSender_option">
                    <InsertEmoticonIcon style={{ color: "orange"}} />
                    <h3>Feeling/Activity</h3>
                </div>
				
				<Dialog
					fullWidth={fullWidth}
        			maxWidth={'xs'}
        			open={open}
        			onClose={handleClose}
        			aria-labelledby="max-width-dialog-title"
      			>
				<DialogTitle id="max-width-dialog-title">
				  <div className="dialog_head" onClick={handbut} onload={hidframe}>
					<Button variant={!butval ? "outlined":"contained" } size={maxWidth ? 'small':'medium'} color="primary">
						Upload Image
					</Button>
					<Button variant={butval ? "outlined":"contained" } size={maxWidth ? 'small':'medium'} color="primary">
						Upload Video
					</Button>
				  </div>
				</DialogTitle>
				<DialogContent>
				<div className="dialog" style={{display: 'flex'}} onLoad={handleDial}>
				
				<div className="dialog0">
				<img  id="previewImg" className="dialog0-img" src="" />
				<video id="previewVid" className="dialog0-vid" controls>
					<source src="" />
				</video>
				
				<div className="dialog1" >
				<input
        				accept="image/*"
        				className="upFile"
        				id="contained-button-file"
        				multiple
        				type="file"
						style={{display:'none'}}
						onChange={handlePrev1}
      			/>
				<label htmlFor="contained-button-file">
					<Button variant="contained" color="primary" size={maxWidth ? 'small':'medium'} component="span" >
						Choose image
					</Button>
				</label>
					<Button variant="contained" color="primary" size={maxWidth ? 'small':'medium'} onClick={handleFile} >
						Upload image
					</Button>
				</div>
				<div className="dialog2" >
				<input
        				accept="video/*"
        				className="upFile"
        				id="contained-button-file1"
        				multiple
        				type="file"
						style={{display:'none'}}
						onChange={handlePrev2}
      			/>
				<label htmlFor="contained-button-file1">
					<Button variant="contained" color="primary" size={maxWidth ? 'small':'medium'} component="span" >
						Choose video
					</Button>
				</label>
					<Button variant="contained" color="primary" size={maxWidth ? 'small':'medium'} onClick={handleFile1}>
						Upload video
					</Button>
            </div>
				</div>
				</div>
				</DialogContent>
				</Dialog>
            </div>

        </div>
    )
}

export default MessageSender
