import React from 'react' ;
import ThumbUpIcon from "@material-ui/icons/ThumbUp";
import ShareIcon from "@material-ui/icons/Share";
import "./PostBar.css";
import db from "./firebase";
import { useStateValue } from "./StateProvider";
import CommentBox from './CommentBox';


function PostBar({like, comment, share, posid, totlik, totcom, totsha, comtime, comarr, comuser, compic }) {
    
    const [{user}, dispatch] = useStateValue();
    var comtime1 = comtime;
    const username = user.displayName;
    var likarr1 = like;
    var bole = likarr1.includes(username);
    var liksty = { color: 'gray'};
    if(bole == true)
    {        
        liksty = { color: '#2e81f4'};
    }
    
    const inclike = (e) => {
        var totlike = 0;
        var likarr = like;
        var emp = "a1";
        
        e.preventDefault();

        var bol = likarr.includes(username) ;
        var userr = likarr.includes("a1");
        var userx = 0;

        if(bol == false)
        {
            if(userr == true)
            {
                userx = likarr.indexOf("a1");
                likarr[userx] = username;
            }
            else
            {
                likarr.push(username);
            }
            document.getElementById("likic").style.color="red";
            totlike = totlik + 1;
            db.collection('posts').doc(posid).update ({ 
            totlik: totlike,
            like: likarr
            })
        }
        else
        {
            document.getElementById("likic").style.color="gray";
            userx = likarr.indexOf(username);
            likarr[userx] = emp;
            totlike = totlik - 1;
            db.collection('posts').doc(posid).update ({
            totlik: totlike,
            like: likarr
            })
        }
        
    }
    
        return (
        <div className="post_options" >
                <div className="post_option" id="likic" style={liksty} onClick={inclike} >
                    <p> {totlik} </p>
                    <ThumbUpIcon />
                    <p>Like</p>
                </div>

                <div className="post_option">
                    <p> {comment} </p>
                    <CommentBox 
                         comtime = {comtime}
                         comment = {comment}
                         comarr = {comarr}
                         comuser = {comuser}
                         compic = {compic}
                         username = {username}
                         posid = {posid}
                    />
                    <p>Comment</p>
                </div>

                <div className="post_option">
                    <p> {totsha} </p>
                    <ShareIcon />
                    <p>Share</p>
                </div>
        </div> 
    )
}

export default PostBar
