import { Avatar } from '@material-ui/core';
import React from 'react';
import "./ComPost.css";

function ComPost({ posid,  comarr, comuser, compic }) {
    
    return (
        <div className="post1">
            <div className="post_top1">
                <Avatar src={compic}
                className="post_avatar1" />
                <div className="post_topInfo1">
                    <h3> {comuser} </h3>
                    <p>{comarr}</p>
                </div>
            </div>

            
        </div>
    )
}

export default ComPost
