import React from "react";
import "./App.css";
import Header from "./Header";
import Sidebar from "./Sidebar";
import Feed from "./Feed"; 
import { useStateValue } from "./StateProvider";
import Login from "./Login";

function App() {
	//document.addEventListener('contextmenu',event => {           //To disable right click
	//	event.preventDefault()
	//}
	//	);
  const [{ user},dispatch] = useStateValue();
  
  
  const handleScreen = () => {
	  var scr_wid = window.innerWidth;
	  var dev1 = navigator.userAgent;
	  if(navigator.userAgent.match(/IEMobile|Windows/i) != "Windows")
	  {
		  document.getElementsByClassName("app")[0].style.zoom = 0.75;
		  document.getElementsByClassName("header_left")[0].style.display = "none";
		  document.getElementsByClassName("header_input")[0].style.display = "none";
		  document.getElementsByClassName("header_info")[0].style.display = "none";
		  document.getElementsByClassName("messageSender_bottom")[0].style.fontSize = "small";
		  document.getElementsByClassName("sidebar")[0].style.display = "none";
		  if(window.outerWidth < window.outerHeight)
		  {
			scr_wid = window.innerWidth;
		  }
		  else {
			  scr_wid = window.innerHeight;
		  }
	  }
		  	
		  document.getElementsByClassName("feed")[0].style.width = scr_wid-100;
		  document.getElementsByClassName("header")[0].style.width = scr_wid-100;
		  document.getElementsByClassName("messageSender")[0].style.width = scr_wid-300;
  }
  
  return (
    ///BEM naming convention
    <div className="app">
      {!user ? (
        <Login />
      ) : (
        <>
        <Header />

        <div className="app_body" onLoad={handleScreen}>
          <Sidebar />
          <Feed />
        </div>
        </>
      )}
	</div>
  );
}

export default App;
