import React, { useEffect, useState } from 'react' ;
import { makeStyles } from '@material-ui/core/styles';
import { Avatar } from '@material-ui/core';
import {useStateValue} from "./StateProvider";
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import Slide from '@material-ui/core/Slide';
import ChatBubbleOutlineIcon from "@material-ui/icons/ChatBubbleOutline";
import db from "./firebase";
import firebase from "firebase";
import "./CommentBox.css";
import SendIcon from '@material-ui/icons/Send';
import ComPost from "./ComPost";
import { Directions } from '@material-ui/icons';

const useStyles = makeStyles((theme) => ({
  form: {
    display: 'flex',
    flexDirection: 'column',
    margin: 'auto',
    width: 'fit-content',
  },
  formControl: {
    marginTop: theme.spacing(2),
    minWidth: 120,
  },
  formControlLabel: {
    marginTop: theme.spacing(1),
  },
}));

function CommentBox({comtime, comment,comarr, comuser, compic, username, posid}) {
  
  const [{user}, dispatch] = useStateValue();
  const[input, setInput] = useState("");
  
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [fullWidth, setFullWidth] = React.useState(true);
  const [maxWidth, setMaxWidth] = React.useState('md');
  var postarr=new Array();
  var posdata = new Object();

  const [posts,setPosts] = useState([]);
    useEffect(() =>{
      db.collection("posts").doc(posid).collection("msg").orderBy("comtime","desc").onSnapshot(snapshot => (
          setPosts(snapshot.docs.map((doc) => ({ id: doc.id, data: doc.data()})))
      ));
  }, [])

  const handleClickOpen = () => {
    setOpen(true);
    
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleUpdClose1 = () => {
    setOpen(false);
        var totcomm = comment;
        var comarr1 = comarr;
        var comtime1 = comtime;
        var comuser1 = comuser;
        var compic1 = compic;
        var userr = comarr1.includes("a1") ;
        var time0 = new Date();
        var userx = 0;

            if(userr == true)
            {
                userx = comarr1.indexOf("a1");
                comarr1[userx] = input;
                comtime1[userx] = time0;
                compic1[userx] = user.photoURL;
                comuser1[userx] = user.displayName;
            }
            else
            {
                comarr1.push(input);
                comtime1.push(time0);
                compic1.push(user.photoURL);
                comuser1.push(user.displayName)
            }
            
            totcomm = totcomm + 1;
            
            db.collection('posts').doc(posid).update ({ 
            comment: totcomm,
            comarr: comarr1,
            comtime: comtime1,
            compic: compic1,
            comuser: comuser1
            })
            setInput("");        
  };
  const handleUpdClose = (e) => {
    var totcomm = comment;
    totcomm += 1;
    
    e.preventDefault();

    db.collection('posts').doc(posid).update ({ 
      comment: totcomm
    })
  var stdtime = new Date();
  stdtime = stdtime.toLocaleString();
  db.collection('posts').doc(posid).collection('msg').doc().set ({
    comments: input,
    profilePic: user.photoURL,
    username: user.displayName,
	comtime: firebase.firestore.FieldValue.serverTimestamp(),
	comstdtime: stdtime
})    
            setInput("");  
};
  

  return (
    <React.Fragment>
      <Button onClick={handleClickOpen}>
          <ChatBubbleOutlineIcon />
      </Button>
      <Dialog
        fullWidth={fullWidth}
        maxWidth={maxWidth}
        open={open}
        onClose={handleClose}
        aria-labelledby="max-width-dialog-title"
      >

        <div className="messageSender1">
            <div className="messageSender_top1">
                <Avatar src={user.photoURL}/>
                <form>
                    <input
                        value={input}
                        onChange={(e) => setInput(e.target.value)}
                        className="messageSender_input1"
                        placeholder={`Your comment goes here`} 
                    />
                    <button onClick={handleUpdClose} type="submit" style={{display:'none'}}> Hidden submit </button>
                <Button onClick={handleUpdClose} color="secondary">
                    <SendIcon />
                </Button>
                </form>
            </div>
        </div>
            
            { posts.map(post => (
                <ComPost 
                  posid={post.id}
                comarr={post.data.comments}
                comuser={post.data.username}
                compic={post.data.profilePic}
                />
            ))}
        
      </Dialog>
    </React.Fragment>
  );
}

export default CommentBox